




import Group from 'vux/src/components/group/index.vue'
import Selector from 'vux/src/components/selector/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'

import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

import { mapState , mapGetters, mapMutations } from 'vuex'
export default {
name:'information',
props:['numBox','numBag'],
components:{
Group,
Selector,
Datetime,
XTextarea,
Flexbox,
FlexboxItem,
XButton,
Cell,
XTextarea
},
data(){
return{
dateValue:'',    //
titD:""+`<span style="color:red">*</span>`,   //*
textValue:'',   //
}
},
computed:{
...mapState(['chooseStoreObj'])
},
watch:{
},
methods:{

...mapMutations(['cancelChooseList']),
tj(){
if(!this.dateValue){
this.$vux.alert.show({
title: '',
content: '',
})
}else{
const _this = this
this.$vux.confirm.show({
title:"",
content:"",
onCancel () {      //

},
onConfirm () {    //
console.log(_this.tjProductList)
_this.$axios.get(_this.url+'preorderKaController.do?saveKaddcx',{
params:{
userName:localStorage.userName,
passWord:localStorage.passWord,
customerCode :_this.chooseStoreObj.customerCode,
storescode:_this.chooseStoreObj.storescode,




products:JSON.stringify(_this.tjProductList) ,  //
dbillDate:_this.dateValue,  //
vnote:_this.textValue     
}
}).then(res=>{
console.log(res)
if(res.data.success == true){
_this.cancelChooseList()      
_this.textValue =""
_this.$vux.toast.show({
time:'1500',
type:'success',
text: res.data.msg
})
setTimeout(() => {
_this.$router.push({path:"/"})
}, 1500);

}else{
_this.$vux.toast.show({
time:'1500',
type:'cancel',
text: res.data.msg
})
}
}).catch(e=>{
_this.$vux.alert.show({
title: '',
content: ',',
})
})
}
})

}
}

},
computed:{
...mapState(['tjProductList'])
},


















}



